import React, { Component, Fragment } from 'react';
import usb from 'usb';
import fs from 'fs';
import { EtherKeyPair, EtherTransaction } from 'walletcs/src/index';

import { CheckPrivateFlash } from '../Account';
import Button from '../Button';
import SelectTransaction from './SelectTransaction';

import arrow from '../../assets/Arrow.png';
import device from '../../assets/Device.png';
import transactionFlash from '../../assets/transaction_drive.png';
import success from '../../assets/success.png';

import styles from '../Account/index.css';
import tableStyles from '../Validate/index.css';

const drivelist = require('drivelist');

const PRIVATE_KEY_PREFIX = 'prv-';
const PUBLIC_KEY_PREFIX = 'addr-';
const TRANSACTION_PREFIX = 'tr-';
const SIGNED_TRANSACTION_PREFIX = 'signed-';

const CheckTransactionFlash = ({ onCancel }) => (
  <Fragment>
    <div className={styles.icons}>
      <img
        src={transactionFlash}
        className={styles.icon}
        alt=''
      />
      <img
        src={arrow}
        className={styles.icon}
        alt=''
      />
      <img
        src={device}
        className={styles.icon}
        alt=''
      />
    </div>
    <div className={styles.insertPrivate}>Insert flash drive with Transactions to sign</div>
    <div className={styles.controls}>
      <Button onClick={onCancel}>
        Cancel
      </Button>
    </div>
  </Fragment>
);

const DetectPrivateKeys = props => {
  const keys = props.getPrivateKeys() || [];
  const isKeysExists = !!keys.length;

  return (
    <Fragment>
      <div>
        {isKeysExists ?
          <Fragment>
            <div className={tableStyles.tableRow}>
              <div className={tableStyles.tableCell} />
              <div className={tableStyles.tableCell} style={{ color: '#ABABAB' }}>ACCOUNT</div>
              <div className={tableStyles.tableCell} style={{ color: '#ABABAB' }}>TIME</div>
              <div className={tableStyles.tableCell} style={{ color: '#ABABAB' }}>KEY FOUND</div>
            </div>
            <Fragment>
              {keys.map(item => (
                <div className={tableStyles.tableRow}>
                  <div className={tableStyles.tableCell} />
                  <div className={tableStyles.tableCell}>{item.account}</div>
                  <div className={tableStyles.tableCell} />
                  <div className={tableStyles.tableCell}>{item.found ? 'Yes' : 'No'}</div>
                </div>
              ))}
            </Fragment>
          </Fragment>
        :
          <div className={tableStyles.message}>Private keys not found</div>
        }
      </div>
      <div className={styles.rowControls}>
        <Button onClick={props.onCancel}>
          Cancel
        </Button>
        {isKeysExists &&
          <Button onClick={props.onSave} primary>
            Sign
          </Button>
        }
      </div>
    </Fragment>
  );
}

const SelectTransactionsForSign = props => {
  const transactions = props.getTransactionsForSign() || [];
  const isTransactionsExists = !!transactions.length;

  return (
    <Fragment>
      <div>
        {isTransactionsExists ?
          <Fragment>
            <div className={tableStyles.tableRow}>
              <div className={tableStyles.tableCell} />
              <div className={tableStyles.tableCell} style={{ color: '#ABABAB' }}>FILE</div>
              <div className={tableStyles.tableCell} style={{ color: '#ABABAB' }}>TIME</div>
              <div className={tableStyles.tableCell} style={{ color: '#ABABAB' }}>DETAILS</div>
            </div>
            <Fragment>
              {transactions.map((item, index) => (
                <div className={tableStyles.tableRow}>
                  <div className={tableStyles.tableCell} />
                  <div className={tableStyles.tableCell}>{item.file}</div>
                  <div className={tableStyles.tableCell} />
                  <div className={tableStyles.tableCell} />
                </div>
              ))}
            </Fragment>
          </Fragment>
        :
          <div className={tableStyles.message}>Private keys not found</div>
        }
      </div>
      <div className={styles.rowControls}>
        <Button onClick={props.onCancel}>
          Cancel
        </Button>
        {isTransactionsExists &&
          <Button onClick={props.onSave} primary>
            Save
          </Button>
        }
      </div>
    </Fragment>
  );
}

const Final = ({ getSignedTransactions, onCancel }) => {
  const transactions = getSignedTransactions() || [];

  return (
    <Fragment>
      <div className={styles.container}>
        <img
          src={success}
          className={styles.icon}
          alt=''
        />
        {transactions.length ?
          <Fragment>
            <div className={tableStyles.message}>You have successfully signed {transactions.length} transactions</div>
            {transactions.map(item => (
              <div className={tableStyles.message}>{item}</div>
            ))}
          </Fragment>
        :
          <div className={tableStyles.message}>Transactions not signed</div>
        }
      </div>
      <div className={styles.rowControls}>
        <Button onClick={onCancel} primary>
          Done
        </Button>
      </div>
    </Fragment>
  )
}

export default class SignTransaction extends Component {
  state = {
    activeStep: 0,
    loadingMsg: null
  }

  async componentWillMount() {
    usb.on('attach', async () => {
      const interval = setInterval(async () => {
        await this.getDrivePath();

        if (this.drivePath) {
          clearInterval(interval);
        }
      }, 1000);
    });

    await this.getDrivePath();
  }

  steps = [
    { step: 0, component: CheckTransactionFlash },
    { step: 1, component: SelectTransaction, extraProps: {
      getTransactions: () => this.getTransactions(),
      onSave: () => this.setState({ activeStep: this.state.activeStep + 1 })
    }},
    { step: 2, component: CheckPrivateFlash },
    { step: 3, component: DetectPrivateKeys, extraProps: {
      getPrivateKeys: () => this.getPrivateKeys(),
      onSave: () => {
        this.setState({ activeStep: this.state.activeStep + 1 })
      }
    }},
    { step: 4, component: CheckTransactionFlash },
    { step: 5, component: SelectTransactionsForSign, extraProps: {
      getTransactionsForSign: () => this.getTransactionsForSign(),
      onSave: async () => {
        await this.signTransactions();
        this.setState({ activeStep: this.state.activeStep + 1 })
      }
    } },
    { step: 6, component: Final, extraProps: {
      getSignedTransactions: () => this.getSignedTransactions()
    }}
  ]

  getSignedTransactions = () => {
    return this.signedTransactions;
  }

  getTransactionsForSign = () => {
    return [this.transactions[0]];
  }

  signTransactions = async () => {
    const privateKeyDef = this.privateKeys.find(item => item.found);
    const fullTransaction = this.transactions[0];
    const { transaction: data } = fullTransaction;
    const signedTransactionsData = await Promise.all(data.transactions.map(async tr => {
      const transactionData = tr.transaction;
      return await EtherTransaction.sign(privateKeyDef.privateKey, transactionData);
    }));

    const signedTransaction = { ...fullTransaction.transaction, transactions: signedTransactionsData };
    
    const signedTransactionFile = `${SIGNED_TRANSACTION_PREFIX}${privateKeyDef.account}.json`;
    const path = `${this.drivePath}/${signedTransactionFile}`
    let fd;
    this.signedTransactions = [signedTransactionFile];

    try {
      fd = fs.openSync(path, 'wx+');
      fs.writeFileSync(path, JSON.stringify(signedTransaction), { flag: 'rs+' });
    } catch (error) {
      console.error(error);
    } finally {
      fs.closeSync(fd);
    }
  }

  getPrivateKeys = () => {
    let dir = [];

    try {
      dir = fs.readdirSync(this.drivePath) || [];
    } catch (error) {
      console.error(error);
    }

    this.privateKeys = dir.filter(file => file.includes(PRIVATE_KEY_PREFIX)).map(file => {
      let privateKey;

      try {
        privateKey = fs.readFileSync(`${this.drivePath}/${file}`, 'utf-8');
      } catch (error) {
        console.error(error);
      }

      const { transaction } = this.transactions[0];
      const found = EtherKeyPair.checkPair(transaction.pub_key, privateKey);

      return {
        privateKey,
        found,
        account: file.replace(PRIVATE_KEY_PREFIX, '').replace('.txt', '')
      }
    });

    return this.privateKeys;
  }

  getTransactions = () => {
    let dir = [];

    try {
      dir = fs.readdirSync(this.drivePath) || [];
    } catch (error) {
      console.error(error);
    }

    this.transactions = dir.filter(file => file.includes(TRANSACTION_PREFIX)).map(file => {
      let transaction;

      try {
        transaction = fs.readFileSync(`${this.drivePath}/${file}`, 'utf-8');
      } catch (error) {
        console.error(error);
      }

      return {
        transaction: JSON.parse(transaction),
        file
      }
    });

    return this.transactions;
  }

  getDrivePath = async () => {
    const drive = await this.getDrive() || {};
    const { activeStep } = this.state;

    this.drivePath = ((drive.mountpoints || [])[0] || {}).path;

    if ((activeStep === 0 || activeStep === 2 || activeStep === 4) && this.drivePath) {
      this.setState({ activeStep: activeStep + 1 });
    }
  }

  getDrive = async () => {
    const drivesList = await drivelist.list();
    const drives = drivesList.filter(item => item.busType === 'USB');
    const { activeStep } = this.state;
    const driveType = activeStep === 2 ? 'private' : 'transaction';

    return drives.find(driveItem => {
      let dir = [];
      const { path } = (driveItem.mountpoints[0] || {});

      if (path) {
        try {
          dir = fs.readdirSync(path) || [];
        } catch (error) {
          console.error(error);
        }
      }

      if (driveType === 'private' && !dir.filter(file => file.includes(PUBLIC_KEY_PREFIX)).length) {
        return true;
      }

      if (driveType === 'transaction' && dir.filter(file => file.includes(TRANSACTION_PREFIX)).length) {
        return true;
      }

      return false;
    })
  }

  render() {
    const { activeStep, addressName, loadingMsg } = this.state;
    const Content = this.steps[activeStep].component;

    return (
      <div className={styles.container}>
        <Content
          {...this.props}
          {...this.steps[activeStep].extraProps}
          addressName={addressName}
          loadingMsg={loadingMsg}
        />
      </div>
    );
  }
}
