import React, { Component, Fragment } from 'react';
import usb from 'usb';
import fs from 'fs';

import { CheckPrivateFlash } from '../Account';
import Button from '../Button';

import emptyDrive from '../../assets/empty_drive.png';
import arrow from '../../assets/Arrow.png';
import device from '../../assets/Device.png';
import success from '../../assets/success.png';
import keys from '../../assets/Keys.png';

import styles from '../Account/index.css';
import selfStyles from '../Validate/index.css';

const drivelist = require('drivelist');

const PRIVATE_KEY_PREFIX = 'prv-';
const PUBLIC_KEY_PREFIX = 'addr-';

const CheckEmptyFlash = props => (
  <Fragment>
    <div className={styles.icons}>
      <img
        src={emptyDrive}
        className={styles.icon}
        alt=''
      />
      <img
        src={arrow}
        className={styles.icon}
        alt=''
      />
      <img
        src={device}
        className={styles.icon}
        alt=''
      />
    </div>
    <div style={{ backgroundColor: '#95b4d0', padding: 10, bottom: '10%' }}>
      <div className={styles.publicPoints}>1. Remove PRIVATE key flash drive</div>
      <div className={styles.publicPoints}>2. Make sure you choose the right flash drive</div>
      <div className={styles.publicPoints}>3. insert Empty flash drive</div>
    </div>
    <div className={styles.insertPrivate}>Insert <span className={styles.private}>Empty</span> key flash drive</div>
    <div className={styles.controls}>
      <Button onClick={props.onCancel}>
        Cancel
      </Button>
    </div>
  </Fragment>
);

const CopyKeys = props => (
  <Fragment>
    <div className={styles.icons}>
      <img
        src={keys}
        className={styles.icon}
        alt=''
      />
    </div>
    <div className={styles.insertPrivate}>Private Key flash drive copied</div>
    <div className={styles.rowControls}>
      <Button onClick={props.onCancel}>
        Cancel
      </Button>
      <Button onClick={props.onSave} primary>
        Next
      </Button>
    </div>
  </Fragment>
);

const Final = ({ onCancel }) => (
  <Fragment>
    <div className={styles.container}>
      <img
        src={success}
        className={styles.icon}
        alt=''
      />
      <div className={selfStyles.message}>You have successfully created backed up your PRIVATE key Drive</div>
    </div>
    <div className={styles.rowControls}>
      <Button onClick={onCancel} primary>
        Done
      </Button>
    </div>
  </Fragment>
);

export default class Account extends Component {
  state = {
    activeStep: 0,
    loadingMsg: null
  }

  async componentWillMount() {
    usb.on('attach', async () => {
      const interval = setInterval(async () => {
        await this.getDrivePath();

        if (this.drivePath) {
          clearInterval(interval);
        }
      }, 1000);
    });

    await this.getDrivePath();
  }

  steps = [
    { step: 0, component: CheckPrivateFlash },
    { step: 1, component: CopyKeys, extraProps: {
      onSave: () => this.setState({ activeStep: this.state.activeStep + 1 })
    }},
    { step: 2, component: CheckEmptyFlash },
    { step: 3, component: Final }
  ]

  backupPrivateKeys = () => {
    const keysForRestore = this.privateKeys;

    keysForRestore.forEach(k => {
      const { account } = k;
      const path = `${this.drivePath}/${PRIVATE_KEY_PREFIX}${account}.txt`;
      let fd;

      try {
        fd = fs.openSync(path, 'wx+');
        fs.writeFileSync(path, k.privateKey, { flag: 'rs+' });
      } catch (error) {
        console.error(error);
      } finally {
        fs.closeSync(fd);
      }
    });
  }

  getPrivateKeys = () => {
    let dir = [];

    try {
      dir = fs.readdirSync(this.drivePath) || [];
    } catch (error) {
      console.error(error);
    }

    this.privateKeys = dir.filter(file => file.includes(PRIVATE_KEY_PREFIX)).map(file => {
      let privateKey;

      try {
        privateKey = fs.readFileSync(`${this.drivePath}/${file}`, 'utf-8');
      } catch (error) {
        console.error(error);
      }

      return {
        privateKey,
        account: file.replace(PRIVATE_KEY_PREFIX, '').replace('.txt', '')
      }
    });

    return this.privateKeys;
  }

  getDrivePath = async () => {
    const drive = await this.getDrive() || {};
    const { activeStep } = this.state;

    this.drivePath = ((drive.mountpoints || [])[0] || {}).path;

    if ((activeStep === 0 || activeStep === 2) && this.drivePath) {

      if (activeStep === 0) {
        this.getPrivateKeys();
      }

      if (activeStep === 2) {
        this.backupPrivateKeys();
      }

      this.setState({ activeStep: activeStep + 1 });
    }
  }

  getDrive = async () => {
    const drivesList = await drivelist.list();
    const drives = drivesList.filter(item => item.busType === 'USB');
    const { activeStep } = this.state;
    const driveType = activeStep === 0 ? 'private' : 'empty';

    return drives.find(driveItem => {
      let dir = [];
      const { path } = (driveItem.mountpoints[0] || {});

      if (path) {
        try {
          dir = fs.readdirSync(path) || [];
        } catch (error) {
          console.error(error);
        }
      }

      if (driveType === 'private' &&
        dir.filter(file => file.includes(PRIVATE_KEY_PREFIX)).length &&
        !dir.filter(file => file.includes(PUBLIC_KEY_PREFIX)).length)
      {
        return true;
      }

      if (driveType === 'empty' &&
        !dir.filter(file => file.includes(PRIVATE_KEY_PREFIX)).length &&
        !dir.filter(file => file.includes(PUBLIC_KEY_PREFIX)).length)
      {
        return true;
      }

      return false;
    })
  }

  render() {
    const { activeStep, addressName, loadingMsg } = this.state;
    const Content = this.steps[activeStep].component;

    return (
      <div className={styles.container}>
        <Content
          {...this.props}
          {...this.steps[activeStep].extraProps}
          addressName={addressName}
          loadingMsg={loadingMsg}
        />
      </div>
    );
  }
}
