import React, { Component, Fragment } from 'react';

import Button from '../Button';

import styles from '../Account/index.css';
import tableStyles from '../Validate/index.css';

export default class SelectTransaction extends Component {
  render() {
    const transactions = this.props.getTransactions() || [];
    const isKeysExists = !!transactions.length;

    return (
      <Fragment>
        <div style={{ fontSize: 16 }}>Select transaction to sign</div>
        <div>
          {isKeysExists ?
            <Fragment>
              <div className={tableStyles.tableRow}>
                <div className={tableStyles.tableCell} />
                <div className={tableStyles.tableCell} style={{ color: '#ABABAB' }}>FILE</div>
                <div className={tableStyles.tableCell} style={{ color: '#ABABAB' }}>TIME</div>
                <div className={tableStyles.tableCell} />
              </div>
              <Fragment>
                {transactions.map(item => (
                  <div className={tableStyles.tableRow}>
                    <div className={tableStyles.tableCell} />
                    <div className={tableStyles.tableCell}>{item.file}</div>
                    <div className={tableStyles.tableCell} />
                    <div className={tableStyles.tableCell} />
                  </div>
                ))}
              </Fragment>
            </Fragment>
          :
            <div className={tableStyles.message}>Transactions not found</div>
          }
        </div>
        <div className={styles.rowControls}>
          <Button onClick={this.props.onCancel}>
            Cancel
          </Button>
          {isKeysExists &&
            <Button onClick={this.props.onSave} primary>
              Next
            </Button>
          }
        </div>
      </Fragment>
    );
  }
}