import React, { Component, Fragment } from 'react';
import usb from 'usb';
import fs from 'fs';
import { EtherKeyPair } from 'walletcs/src/index';

import { CheckPrivateFlash, CheckPublicFlash } from '../Account';
import Button from '../Button';
import DetectPublicAddresses from './DetectPublicAddresses';

import success from '../../assets/success.png';
import styles from '../Account/index.css';
import selfStyles from './index.css';

const drivelist = require('drivelist');

const PRIVATE_KEY_PREFIX = 'prv-';
const PUBLIC_KEY_PREFIX = 'addr-';

const DetectPrivateKeys = props => {
  const keys = props.getPrivateKeys() || [];
  const isKeysExists = !!keys.length;

  return (
    <Fragment>
      <div>
        {isKeysExists ?
          <Fragment>
            <div className={selfStyles.tableRow}>
              <div className={selfStyles.tableCell} style={{ color: '#ABABAB' }}>ACCOUNT</div>
              <div className={selfStyles.tableCell} style={{ color: '#ABABAB' }}>ADDRESS</div>
            </div>
            <Fragment>
              {keys.map(item => (
                <div className={selfStyles.tableRow}>
                  <div className={selfStyles.tableCell}>{item.account}</div>
                  <div className={selfStyles.tableCell}>{item.publicKey}</div>
                </div>
              ))}
            </Fragment>
          </Fragment>
        :
          <div className={selfStyles.message}>Private keys not found</div>
        }
      </div>
      <div className={styles.rowControls}>
        <Button onClick={props.onCancel}>
          Cancel
        </Button>
        {isKeysExists &&
          <Button onClick={props.onSave} primary>
            Next
          </Button>
        }
      </div>
    </Fragment>
  );
}

const Final = ({ getRestoredKeys, onCancel }) => {
  const accounts = (getRestoredKeys() || [])

  return (
    <Fragment>
      <div className={styles.container}>
        <img
          src={success}
          className={styles.icon}
          alt=''
        />
        {accounts.length ?
          <Fragment>
            <div className={selfStyles.message}>{accounts.length} PUBLIC ADDRESSES generated:</div>
            {accounts.map(item => (
              <div className={selfStyles.message}>{`${PUBLIC_KEY_PREFIX}${item.account}.txt`}</div>
            ))}
          </Fragment>
        :
          <div className={selfStyles.message}>There is no keys for restore.</div>
        }
      </div>
      <div className={styles.rowControls}>
        <Button onClick={onCancel} primary>
          Done
        </Button>
      </div>
    </Fragment>
  )
}

export default class Account extends Component {
  state = {
    activeStep: 0,
    loadingMsg: null
  }

  async componentWillMount() {
    usb.on('attach', async () => {
      const interval = setInterval(async () => {
        await this.getDrivePath();

        if (this.drivePath) {
          clearInterval(interval);
        }
      }, 1000);
    });

    await this.getDrivePath();
  }

  steps = [
    { step: 0, component: CheckPrivateFlash },
    { step: 1, component: DetectPrivateKeys, extraProps: {
      getPrivateKeys: () => this.getPrivateKeys(),
      onSave: () => this.setState({ activeStep: this.state.activeStep + 1 })
    }},
    { step: 2, component: CheckPublicFlash },
    { step: 3, component: DetectPublicAddresses, extraProps: {
      getPublicKeys: () => this.getPublicKeys(),
      onSave: generate => {
        this.restorePublicKey(generate);
        this.setState({ activeStep: this.state.activeStep + 1, generate })
      }
    }},
    { step: 4, component: Final, extraProps: {
      getRestoredKeys: () => this.getRestoredKeys(this.state.generate)
    }}
  ]

  getRestoredKeys = generate => generate ? this.publicKeys.filter(k => !k.found) : [];

  restorePublicKey = generate => {
    const keysForGenerate = this.getRestoredKeys(generate);

    keysForGenerate.forEach(k => {
      const address = EtherKeyPair.recoveryPublicKey(k.privateKey);
      const { account } = k;
      const path = `${this.drivePath}/${PUBLIC_KEY_PREFIX}${account}.txt`;
      let fd;

      try {
        fd = fs.openSync(path, 'wx+');
        fs.writeFileSync(path, address, { flag: 'rs+' });
      } catch (error) {
        console.error(error);
      } finally {
        fs.closeSync(fd);
      }
    });
  }

  getPublicKeys = () => {
    let publicKeysFiles = [];

    try {
      publicKeysFiles = fs.readdirSync(this.drivePath) || []
    } catch (error) {
      console.error(error);
    }

    const publicKeys = publicKeysFiles.map(pkfile => {
      let res;

      try {
        res = fs.readFileSync(`${this.drivePath}/${pkfile}`, 'utf-8');
      } catch (error) {
        console.error(error);
      }

      return res;
    })

    this.publicKeys = this.privateKeys.map(item => ({
      ...item,
      found: publicKeys.includes(item.publicKey)
    }))

    return this.publicKeys;
  }

  getPrivateKeys = () => {
    let dir = [];

    try {
      dir = fs.readdirSync(this.drivePath) || [];
    } catch (error) {
      console.error(error);
    }

    this.privateKeys = dir.filter(file => file.includes(PRIVATE_KEY_PREFIX)).map(file => {
      let privateKey;
      let publicKey;

      try {
        privateKey = fs.readFileSync(`${this.drivePath}/${file}`, 'utf-8');
        publicKey = EtherKeyPair.recoveryPublicKey(privateKey);
      } catch (error) {
        console.error(error);
      }

      return {
        privateKey,
        publicKey,
        account: file.replace(PRIVATE_KEY_PREFIX, '').replace('.txt', '')
      }
    });

    return this.privateKeys;
  }

  getDrivePath = async () => {
    const drive = await this.getDrive() || {};
    const { activeStep } = this.state;

    this.drivePath = ((drive.mountpoints || [])[0] || {}).path;

    if ((activeStep === 0 || activeStep === 2) && this.drivePath) {
      this.setState({ activeStep: activeStep + 1 });
    }
  }

  getDrive = async () => {
    const drivesList = await drivelist.list();
    const drives = drivesList.filter(item => item.busType === 'USB');
    const { activeStep } = this.state;
    const driveType = activeStep === 0 ? 'private' : 'public';

    return drives.find(driveItem => {
      let dir = [];
      const { path } = (driveItem.mountpoints[0] || {});

      if (path) {
        try {
          dir = fs.readdirSync(path) || [];
        } catch (error) {
          console.error(error);
        }
      }

      if (driveType === 'private' && !dir.filter(file => file.includes(PUBLIC_KEY_PREFIX)).length) {
        return true;
      }

      if (driveType === 'public' && !dir.filter(file => file.includes(PRIVATE_KEY_PREFIX)).length) {
        return true;
      }

      return false;
    })
  }

  render() {
    const { activeStep, addressName, loadingMsg } = this.state;
    const Content = this.steps[activeStep].component;

    return (
      <div className={styles.container}>
        <Content
          {...this.props}
          {...this.steps[activeStep].extraProps}
          addressName={addressName}
          loadingMsg={loadingMsg}
        />
      </div>
    );
  }
}
